<template>
    <div>
        <div class="header"><h2>请输入个人信息项</h2></div>
        <div class="tableContainer">
            <div class="tableRow">
                <p>姓名</p>
                <p><input type="text" v-model="person.name"></p>
            </div>
            <div class="tableRow">
                <p>身份证号</p>
                <p><input type="text" v-model="person.idCard"></p>
            </div>
            <div class="tableRow">
                <p>银行卡号</p>
                <p><input type="text" v-model="person.accountNo"></p>
            </div>
            <div class="tableRow">
                <p>手机号</p>
                <p><input type="text" v-model="person.bankPreMobile"></p>
            </div>
        </div>
      <div class="footer" style="float: left;margin-left: 45%;margin-top: 50px"><input id="act1" type="button" @click="check" value="查验"></div>
<!--      <div class="footer" style="float: right;margin-right: 500px;margin-top: 50px"><input type="button" @click="checkRecord" value="查验记录"></div>-->
    </div>
</template>
<script>
    export default{
        name: 'enterpriseCheck',
        data: function () {
            return {
                person:{
                    name: null,
                    idCard: null,
                    accountNo: null,
                    bankPreMobile: null,
                }
            }
        },
        methods: {
            check () {
              // document.getElementById('act1').value = "查验中...";
              let _this = this;
                if ( !this.person.name || !this.person.accountNo) {
                    this.msgError("至少输入银行卡和姓名");
                } else {
                  document.getElementById('act1').value = "查验中...";
                  _this.$request({
                    url: '/check/person',
                    method: 'POST',
                    data: _this.person
                  }).then(response => {
                    // console.log('个人：'+JSON.stringify(response.data));
                    // console.log(response.data.substr(0,7))
                    if (response.code === 200 ) {
                      if (response.data.substr(0,7) === "400 Bad") {
                        _this.msgError("信息格式错误或请求次数已达上限！");
                        document.getElementById('act1').value = "查验";
                        // return;
                      } else {
                        this.$router.push({
                          path: '/report/individual',
                          query:{
                            rules1:JSON.stringify(response.data)
                          }
                        });
                      }
                      // console.log(123)

                      // _this.ruleResult = response.data.ruleLists;
                      // _this.information = response.data.informations;
                    }else {

                    }
                    document.getElementById('act1').value = "查验";
                  }).catch(error => {
                    document.getElementById('act1').value = "查验";
                  });

                }
            },

          checkRecord: function (){
              // this.msgSuccess("查验成功");
            let _this = this;
            _this.$router.push({ path: '/checkList'});
          },

        },
    }
</script>
<style scoped>
    .header{
        text-align: center;
    }
    .tableContainer{
        margin-left: auto;
        margin-right: auto;
        display: table;
        border-spacing: 20px;
    }
    .tableRow{
        display: table-row;
    }
    .tableRow p{
        display: table-cell;
        /*vertical-align: top;*/
    }
    .tableRow p:first-child{
        text-align: right;
        font-size: 20px;
        padding: 5px;
    }
    .tableRow p:nth-child(2) input{
        width: 200px;
        height: 18px;
        padding: 5px;
    }
    .footer{
        text-align: center;
    }
    .footer input{
        width: 200px;
        height: 35px;
        background-color: #0c4469;
        color: white;
        letter-spacing: 3px;
        font-size: 18px;
        border-radius: 5px;
    }
</style>



